import React, { useState, SyntheticEvent } from 'react';
import styles from "./Company.module.css";
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import { TabContext, TabList, TabPanel } from '@mui/lab';
import Directions from './Directions';
import Greeting from './Greeting';
import Introduction from './Introduction';
import Divider from '@mui/material/Divider';
import { useLocation } from 'react-router-dom';
import { useEffect  } from 'react'

function Company () {
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const tab = params.get('tab');

  const [value, setValue] = useState(tab || 'Introduction');

  const handleChange = (event: SyntheticEvent<{}>, newValue: string) => {
    setValue(newValue);
  };

 useEffect(() => {
  const newTabValue = params.get('tab');
  if (newTabValue) {
    setValue(newTabValue)
  }
 }, [location.search])

  return (
    <div>
      {/* 상단 네브바와 겹칠 이미지 */}
      <div className={styles.bigImg} >
        <img 
          src={require('assets/Images/Humandeep/subPages/subMain.png')} 
          alt="" 
          className={styles.imgSizs} 
        />
        <div className={styles.titleTextCard}>
          <p className={styles.smallText}>회사소개</p>
          <p className={styles.bigText}>Company</p>
        </div>
      </div>

      {/* 소개, 인사말 오시는길 선택 */}
      <div className={styles.container}>
        <Box sx={{ width: '100%', typography: 'body1' }}>
          <TabContext value={value} >
            <Box >
              <TabList 
                onChange={handleChange} 
                aria-label="lab API tabs example" 
                TabIndicatorProps={{
                  style: {
                    backgroundColor: "white",
                    color: '#2D5DA6'
                  }
                }}
              >
                <Tab 
                  label="소개" 
                  value="Introduction" 
                  disableRipple
                  sx={{ 
                    fontSize: '20px', 
                    fontWeight: 'bold', 
                  }}
                />
                <Divider 
                  orientation="vertical" 
                  flexItem 
                  sx={{ 
                    bgcolor: '#2D5DA6', 
                    borderRightWidth: 1.5 
                  }}
                />
                <Tab 
                  label="인사말"
                  value="Greeting" 
                  disableRipple
                  sx={{ 
                    fontSize: '20px', 
                    fontWeight: 'bold', 
                    "& button:focus":{color:'#2D5DA6'}
                  }}
                />
                <Divider 
                  orientation="vertical" 
                  flexItem 
                  sx={{ 
                    bgcolor: '#2D5DA6', 
                    borderRightWidth: 1.5 
                  }}
                />
                <Tab 
                  label="오시는길" 
                  value="Directions"
                  disableRipple
                  sx={{ 
                    fontSize: '20px', 
                    fontWeight: 'bold', 
                    "& button:focus":{color:'#2D5DA6'}
                  }} 
                />
              </TabList>
            </Box>

            {/* 각 컴포넌트 오는곳 */}
            <TabPanel value="Introduction"><Introduction /></TabPanel>
            <TabPanel value="Greeting"><Greeting /></TabPanel>
            <TabPanel value="Directions"><Directions /></TabPanel>
          </TabContext>
        </Box>
    </div>
    </div>
  )
}

export default Company;
