import styles from './Navbar.module.scss'

function HugoNavbar() {
  return (
    <nav className={styles.navbar}>
      <div className={styles.navbar_container}>
        <input type="checkbox" name="" id="" />
        <div className={styles.hamburger_lines}>
          <span className={`${styles.line} ${styles.line1}`}></span>
          <span className={`${styles.line} ${styles.line2}`}></span>
          <span className={`${styles.line} ${styles.line3}`}></span>
        </div>
        <ul className={styles.menu_items}>
          <li><a href="#Products">Product</a></li>
          <li><a href="#Labs">Lab</a></li>
          <li><a href="/" style={{textDecoration: 'none'}}>HD</a></li>
        </ul>
        <div className={styles.logo}>
          <a href="/hugo" className={styles.logo_main} style={{textDecoration: 'none', color: '#283c59'}}>
            <span style={{fontWeight: 'bold', fontSize: '28px'}}>HUGO</span>
            <span className={styles.hide_text} style={{fontSize: '16px'}}> :Humandeep Platform</span>
          </a>
        </div>
      </div>
    </nav>
  );
}

export default HugoNavbar;