import styles from "./Introduction.module.css";

const Introduction = () => {
  return(
    <div className={styles.pageStyle}>

      {/* 타이틀 */}
      <div 
        style={{
          fontWeight: 'bold', 
          fontSize: '2.8em', 
          margin: '0px',
          color: '#2D5DA6',
          marginBottom: 45, 
        }}
      >
        휴먼딥 소개
      </div>

      {/* 상단이미지 */}
      <div>
        <img 
          src={require('assets/Images/Humandeep/subPages/Introduction.png')} 
          alt="img" 
          className={styles.imgSizs} 
        />
      </div>

      {/* 중간 타이틀 */}
      <div
        style={{
          fontSize: '1.5em', 
          marginTop: 45, 
        }}
      >
        21세기 기술의 혁신, AI 시스템의 기술력
      </div>
      <div
        style={{
          fontSize: '2.8em', 
          margin: '0px',
          color: '#2D5DA6',
          fontWeight: 'bold', 
          marginBottom: 45, 
        }}
      >
        메디컬 AI의 시작
      </div>

      {/* content */}
      <div className={styles.textStyle}>
        <p><b>Medical AI</b>기업인 ㈜휴먼딥은</p>
        <p><b>AI 기반 위험 예측시스템, 대용량 데이터 수집 및 분석, 데이터 시각화 기반</b>의 고객 특화 솔루션 개발을 통해</p>
        <p>우리 삶을 즐겁고 편안하게 생활하도록 도와드리며, 다양한 업무 환경 속에서</p>
        <p>다가올 미래에 대한 직관적이고 유연한 통찰력을 제공 할 수 있도록</p>
        <p>다양한 기술과정보 제공을 위해 노력하고 있습니다.</p>
      </div>
    </div>
  )
}

export default Introduction