import styles from "./Greeting.module.css";

const Greeting = () => {
  return(
    <div className={styles.pageStyle}>

      {/* 타이틀 */}
      <div 
        style={{
          fontWeight: 'bold', 
          fontSize: '2.8em', 
          margin: '0px',
          color: '#2D5DA6',
          marginBottom: 45,
        }}
      >
        인사말
      </div>

      {/* content */}
      <div className={styles.textStyle}>
        <p style={{ marginBottom: 35  }}>
          인류의 미래를 바꿀 주요 신기술 중의 하나인 인공지능(AI)기술은 이미 우리도 모르는 사이 삶의 곳곳에 이미 적용되고 있습니다. <br />
          그러나 각 현장에서 인공지능을 적용하고 있지만 실제로 어떻게 활용 할 수 있는지에 대하여 그 이해의 폭이 넓지 못한 것은 사실입니다. <br />
          그럼에도 불구하고 다양한 산업 및 지역, 비즈니스에 적용되면서 많은 이들이 혜택을 누리고 있으며, 특히 의료분야에서도 그 중요성이 점점 커지고 있는 현실입니다. <br />
        </p>
        <p style={{ marginBottom: 35  }}>
          우리가 인공지능을 연구하는 이유는 인간의 삶을 보다 즐겁고 편안하게 하는 해줄 수 있는 동반자로 인식하고 있으며, <br />
          우리의 생활에 변화를 줄 수 있는 가치를 알고 있기 때문입니다. <br />
        </p>
        <p style={{ marginBottom: 35  }}>
          우리는 인내와 강인하지만 헌신적인 리더십 속에서 회사의 미래가치를 함께 공유할 것이며, <br />
          목표를 향한 성취와 성공의 가치를 위해 실패 보다 실천하지 못하는 두려움을 싫어 합니다. <br />
        </p>
        <p style={{ marginBottom: 35  }}>
          기업의 자산과 가치는 우리 구성원들입니다. 다시 말하면, 구성원의 가치가 곧 기업의 가치가 됩니다.<br />
          구성원간 소통과 배려, 존중은 기업 문화의 한 축입니다.<br />
          구성원간 기업 정보의 공유, 이익의 공유 그리고 가치의 공유는 다른 또 한 축입니다.<br />
          기업과 기업의 축인 구성원 간의 성장은 기업 가치의 성장이라 확신합니다.<br />
        </p>
        <p>
          인간의 지능을 대신하고자 하는 인간의 노력은 끝이 없는 무한 도전이 될 것입니다.<br />
          우리는 기업 가치를 알기에 이 모든 상황에서 인내하며 성취하고 또 성공의 미래 가치 실현을 위한 도전에<br />
          휴먼딥은 끝까지 함께 할 것입니다.<br />
        </p>
      </div>

      {/* 대표이사 서명 */}
      <div className={styles.imgStyle}>
        <img 
          src={require('assets/Images/Humandeep/subPages/sign.png')} 
          alt="img" 
          className={styles.imgSizs} 
        />
      </div>
    </div>
  )
}

export default Greeting