import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Humandeep from './Humandeep'
import Hugo from './Hugo'

const routers = () => {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/*" element={<Humandeep />} />
        <Route path="/hugo/*" element={<Hugo />} />
      </Routes>
    </BrowserRouter>
  );
};

export default routers;