import React, { useState, useEffect } from 'react';
import styles from "./WebNavbar.module.css";
import Button from '@mui/material/Button';
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import { useLocation } from 'react-router-dom';
import { Link } from 'react-router-dom';

function Webnavbar() {
  const [scrollPosition, setScrollPosition] = useState(0);
  const [anchorEl, setAnchorEl] = useState(null);
  const location = useLocation();

  const handleClick = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    if (anchorEl !== event.currentTarget) {
      setAnchorEl(event.currentTarget as unknown as null);
    }
}

  const handleClose = () => {
    setAnchorEl(null);
  }

  const updateScroll = () => {
    setScrollPosition(window.scrollY || document.documentElement.scrollTop);
    console.log(scrollPosition)
  }

  useEffect(() => {
    window.addEventListener('scroll', updateScroll);
    return () => {
      window.removeEventListener('scroll', updateScroll);
    };
  }, []);

  useEffect(() => {
    setAnchorEl(null);
  }, [location]);

  return (
    <nav className={scrollPosition < 100 ? styles.originalNavbar : styles.scrollNavbar}>
      <div className={styles.navbar_container} >
        <div className={styles.logoDiv}>
          <Link to="/">
          <img src={require('assets/Images/logo/logo_white.png')} alt="title" className={styles.logo} ></img>
          </Link>
        </div>

        <div>
          <Button
            disableRipple
            sx={{
              color: '#2D5DA6',
              width: 100,
              backgroundColor: 'white',
              fontSize: '16px',
              fontWeight: 'lighter',
              borderRadius: 20,
              mr: 2,
              "&:hover": { opacity: 0.6, transition: "opacity .5s", backgroundColor: "white" }
            }}
            href="/hugo"
          >
            Hugo
          </Button>


          <Button
            aria-owns={anchorEl ? "simple-menu" : undefined}
            aria-haspopup="true"
            onClick={handleClick}
            onMouseOver={handleClick}
            disableRipple
            sx={{
              color: 'white',
              width: 100,
              fontSize: '16px',
              fontWeight: 'lighter',
              borderRadius: 20,
              zIndex: 1301,
              "&:hover": { opacity: 0.6, transition: "opacity .5s" }
            }}
            href="/company"
          >
            회사소개
          </Button>
          <Menu
            id="simple-menu"
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleClose}
            autoFocus={false}
            className={styles.dropdownMenu}
            MenuListProps={{ onMouseLeave: handleClose }}
          >
            <MenuItem 
              onClick={handleClose} 
              className={styles.dropdownMenuItem}
              disableRipple
              component={Link} to="/company?tab=Introduction"
            >
              소개
            </MenuItem>
            <MenuItem 
              onClick={handleClose} 
              className={styles.dropdownMenuItem}
              disableRipple
              component={Link} to="/company?tab=Greeting"
            >
              인사말
            </MenuItem>
            <MenuItem 
              onClick={handleClose} 
              className={styles.dropdownMenuItem}
              disableRipple
              component={Link} to="/company?tab=Directions"
            >
              오시는길
            </MenuItem>
          </Menu>
          
          <Button
            disableRipple
            sx={{
              color: 'white',
              width: 100,
              fontSize: '16px',
              fontWeight: 'lighter',
              borderRadius: 20,
              "&:hover": { opacity: 0.6, transition: "opacity .5s" }
            }}
            href="/pr"
          >
            홍보자료
          </Button>
        </div>
      </div>
    </nav>
  );
}

export default Webnavbar;



