import styles from "./Humandeep.module.css";
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, EffectCoverflow, Autoplay } from "swiper";
import SwiperCore from 'swiper';
import "swiper/css";
import "swiper/swiper-bundle.min.css";
import Divider from '@mui/material/Divider';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';


SwiperCore.use([Navigation, EffectCoverflow, Autoplay]);

function HDContents(props: any) {


  return (
    <div className={styles.container}>
      <div id="hdContents" style={{maxWidth: '100%'}}>
        <div id={styles.hdContents}>
          <div className={styles.content}>

            {/* 스와이퍼설정 */}
            <Swiper
              spaceBetween={250}//스와이퍼 사이 공간길이
              slidesPerView={1.7}//한화면에 가운데 있는 슬라이드 갯수 / 길이때문에 1.8로 해둠
              centeredSlides={true} //중앙에 슬라이드 큰거
              onSlideChange={() => console.log('slide change')}
              onSwiper={(swiper) => console.log(swiper)}
              modules={[Navigation]} // 모듈추가
              speed = {700}  //슬라이드 넘어가는 속도
              effect={'coverflow'}  //3d효과
              grabCursor={true}
              loop={true} //슬라이드 루프
              autoplay={{
                delay: 3500,
              }} //3.5초마다 자동으로 돌아감
              coverflowEffect={{
                rotate: 0,
                stretch: 0,
                depth: 200,
                modifier: 1.5,
                slideShadows: false,
              }}

              breakpoints={{
                0: {
                  slidesPerView: 1.2,
                  spaceBetween: 100,
                  centeredSlides: true,
                },
                768: {
                  slidesPerView: 1.5,
                  spaceBetween: 250,
                  centeredSlides: true,
                },
              }}
            >

              {/* 슬라이드 1 */}
              <SwiperSlide className={styles.custom_slide}>
                <img 
                  src={require('assets/Images/Humandeep/lendingPageImg/AI.png')} 
                  alt="" 
                  className={styles.slide_img} 
                />
                <div className={styles.textLine}>
                  <p className={styles.slideSPText}>
                    AI
                  </p>
                  <Divider sx={{ width: '150px' }}/>
                  <p className={styles.slideBigTextWhitMargin}>
                    성공적인 인공지능 비즈니스 도입을 위한 최고의 선택지입니다.
                  </p>
                  <p className={styles.slideBigText}>
                    고객에 필요한 최적의 인공지능을 고민하고 개발합니다.
                  </p>
                </div >

                <div>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/> 아이템에 맞는 인공지능 서비스 컨설팅 제공합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  머신러닝 / 딥러닝.  모든 형태의 인공지능 솔루션 도입이 가능합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  최신 논문 및 연구자료를 활용해 최적화된 인공지능을 개발합니다.
                  </p>
                </div>
              </SwiperSlide>

              {/* 슬라이드 2 */}
              <SwiperSlide className={styles.custom_slide}>
                <img 
                  src={require('assets/Images/Humandeep/lendingPageImg/bigData.png')} 
                  alt="" className={styles.slide_img} 
                />
                <div className={styles.textLine}>
                  <p className={styles.slideSPText}>
                    Big Data
                  </p>
                  <Divider sx={{ width: '150px' }}/>
                  <p className={styles.slideBigTextWhitMargin}>
                    수집 - 전처리 - 분석 - 활용의 파이프라인을 개발하고, 비즈니스에 연결합니다.
                  </p>
                  <p className={styles.slideBigText}>
                    빅데이터의 구축을 넘어 이를 분석하고 활용하는데 중점을 두고 서비스를 기획합니다.
                  </p>
                </div >

                <div>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  1:1 빅데이터 컨설팅 및 분석 보고서를 제공합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  고객의 요구사항에 맞는 빅데이터 구축 솔루션을 제공합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  “쓰레기가 들어오면 쓰레기가 나간다(garbage in, garbage out)”. 우리는 무엇보다 품질을 고민합니다
                  </p>
                </div>
              </SwiperSlide>

              {/* 슬라이드 3 */}
              <SwiperSlide className={styles.custom_slide}>
                <img 
                  src={require('assets/Images/Humandeep/lendingPageImg/Data_Visualization.png')} 
                  alt="" 
                  className={styles.slide_img} 
                />
                <div className={styles.textLine}>
                  <p className={styles.slideSPText}>
                    Data Visualization
                  </p>
                  <Divider sx={{ width: '150px' }}/>
                  <p className={styles.slideBigTextWhitMargin}>
                    Simple is Best. 우리의 기술은 알아보기 쉽고, 사용하기 쉽습니다.
                  </p>
                  <p className={styles.slideBigText}>
                    UI/UX를 고려한 데이터 시각화 기술을 제공합니다.
                  </p>
                </div >

                <div>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  다양한 형태의 차트, 테이블 등을 활용하여 정보를 표현합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  2D, 3D, 워드 클라우드 등 데이터에 적합한 시각화 방법을 고민합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  Desktop application, Web, Moblie App 등 다양한 플랫폼으로 서비스를 제공합니다.
                  </p>
                </div>
              </SwiperSlide>

              {/* 슬라이드 세개만 하면 처음 페이지 로딩됐을때 
              양옆으로 슬라이드가 있지 않아서 중복되는 슬라이드 추가 해 놓았음 */}

              {/* 슬라이드 1 */}
              <SwiperSlide className={styles.custom_slide}>
                <img 
                  src={require('assets/Images/Humandeep/lendingPageImg/AI.png')} 
                  alt="" 
                  className={styles.slide_img} 
                />
                <div className={styles.textLine}>
                  <p className={styles.slideSPText}>
                    AI
                  </p>
                  <Divider sx={{ width: '150px' }}/>
                  <p className={styles.slideBigTextWhitMargin}>
                    성공적인 인공지능 비즈니스 도입을 위한 최고의 선택지입니다.
                  </p>
                  <p className={styles.slideBigText}>
                    고객에 필요한 최적의 인공지능을 고민하고 개발합니다.
                  </p>
                </div >

                <div>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  아이템에 맞는 인공지능 서비스 컨설팅 제공합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  머신러닝 / 딥러닝.  모든 형태의 인공지능 솔루션 도입이 가능합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  최신 논문 및 연구자료를 활용해 최적화된 인공지능을 개발합니다.
                  </p>
                </div>
              </SwiperSlide>

              {/* 슬라이드 2 */}
              <SwiperSlide className={styles.custom_slide}>
                <img 
                  src={require('assets/Images/Humandeep/lendingPageImg/bigData.png')} 
                  alt="" className={styles.slide_img} 
                />
                <div className={styles.textLine}>
                  <p className={styles.slideSPText}>
                    Big Data
                  </p>
                  <Divider sx={{ width: '150px' }}/>
                  <p className={styles.slideBigTextWhitMargin}>
                    수집 - 전처리 - 분석 - 활용의 파이프라인을 개발하고, 비즈니스에 연결합니다.
                  </p>
                  <p className={styles.slideBigText}>
                    빅데이터의 구축을 넘어 이를 분석하고 활용하는데 중점을 두고 서비스를 기획합니다.
                  </p>
                </div >

                <div>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  1:1 빅데이터 컨설팅 및 분석 보고서를 제공합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  고객의 요구사항에 맞는 빅데이터 구축 솔루션을 제공합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  “쓰레기가 들어오면 쓰레기가 나간다(garbage in, garbage out)”. 우리는 무엇보다 품질을 고민합니다
                  </p>
                </div>
              </SwiperSlide>

              {/* 슬라이드 3 */}
              <SwiperSlide className={styles.custom_slide}>
                <img 
                  src={require('assets/Images/Humandeep/lendingPageImg/Data_Visualization.png')} 
                  alt="" 
                  className={styles.slide_img} 
                />
                <div className={styles.textLine}>
                  <p className={styles.slideSPText}>
                    Data Visualization
                  </p>
                  <Divider sx={{ width: '150px' }}/>
                  <p className={styles.slideBigTextWhitMargin}>
                    Simple is Best. 우리의 기술은 알아보기 쉽고, 사용하기 쉽습니다.
                  </p>
                  <p className={styles.slideBigText}>
                    UI/UX를 고려한 데이터 시각화 기술을 제공합니다.
                  </p>
                </div >

                <div>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  다양한 형태의 차트, 테이블 등을 활용하여 정보를 표현합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  2D, 3D, 워드 클라우드 등 데이터에 적합한 시각화 방법을 고민합니다.
                  </p>
                  <p className={styles.slideSmallText}>
                    <FiberManualRecordIcon sx={{fontSize: 'small'}}/>  Desktop application, Web, Moblie App 등 다양한 플랫폼으로 서비스를 제공합니다.
                  </p>
                </div>
              </SwiperSlide>

            </Swiper>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HDContents;



