//스크롤 테스트중  dlwp chltlsdlek!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
import React, { useEffect, useRef } from 'react';
import styles from './Contents.module.scss'
import HDContents from './Humandeep';
import HGContents from './Hugo';
import UpButton from 'components/UpButton';
import Footer from 'pages/Common/Footer'

const Box: React.FC<{ num: number }> = ({ num }) => (
  <div className={styles.box}>
    {num === 1 && (
      <div className={styles.container}>
        <div className={styles.imgSizs}>
          <img src={require('assets/Images/Humandeep/lendingPageImg/Main.png')} alt="main_image" />
          <div className={styles.bannerTextCard}>
            <div>
              <p className={styles.bannerTopTextSize}><span className={styles.bannerBoldText}>AI기반 병실 모니터링 데이터</span>를 이용한</p>
              <p className={styles.bannerTopTextSize}>환자 위험 조기발견 및 예측 시스템</p>
            </div>
            <div className={styles.bannerBottomTextSize}>
              <p>. 인공지능을 이용한 3시간 전 조기 예측</p>
              <p>. 환자 사망률 20% 감소</p>
              <p>. 의료 서비스 질 향상 및 의료비용 감소</p>
            </div>
          </div>
        </div>
      </div>
    )}
    {num === 2 && (
      <div className={styles.container}>
        <HDContents />
      </div>
    )}
    {num === 3 && (
      <div className={styles.container}>
        <HGContents />
      </div>
    )}
    {num === 4 && (
      <div className={styles.emptyContainer}>
        <Footer />
      </div>
    )}
  </div>
)

const Humandeep = () => {
  useEffect(() => {
    const box = Array.from(document.getElementsByClassName(styles.box)) as HTMLElement[];


    const getActiveBox = () => {
      for (let i = box.length - 1; i >= 0; i--) {
        const rect = box[i].getBoundingClientRect();
        if (rect.top < 100) {
          return box[i];
        }
      }
      return box[0];
    };

    box.forEach((el, index) => {
      el.addEventListener("wheel", (event: WheelEvent) => {
        event.preventDefault();
        let delta = 0;
        if (event.deltaY < 0) {
          delta = -1;
        } else if (event.deltaY > 0) {
          delta = 1;
        }

        let moveTop = window.scrollY;
        let currentBox = getActiveBox();
        let currentIndex = box.indexOf(currentBox);

        if (delta > 0) {
          if (currentIndex < box.length - 1) {
            moveTop = box[currentIndex + 1].offsetTop;
          }
        } else {
          if (currentIndex > 0) {
            moveTop = box[currentIndex - 1].offsetTop;
          }
        }

        window.scrollTo({
          top: moveTop,
          behavior: 'smooth'
        });
      })
    })
  }, [])

  return (
    <div className={styles.boxwrap}>
      <Box num={1}/>
      <Box num={2}/>
      <Box num={3}/>
      <Box num={4}/>
      <UpButton />
    </div>
  );
}

export default Humandeep;







