import React, { useState, useEffect } from 'react';
import styles from "./Appbar.module.css";
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import Collapse from '@mui/material/Collapse';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';

function Appbar() {
  // 상단 메뉴버튼 클릭시 Drawer
  const [state, setState] = React.useState({
    left: false,
  });

  //메뉴클릭시 메뉴화면 열기
  const toggleDrawer =
  (open: boolean) =>
  (event: React.KeyboardEvent | React.MouseEvent) => {
    if (
      event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, left: open });
  };

  const [openSubmenu, setOpenSubmenu] = useState(false);

  const handleClick = () => {
    setOpenSubmenu(!openSubmenu);
  };
  
  //메뉴리스트으
  const list = () => (
    <Box
      sx={{ width: 250 , marginTop: 7}}
      role="presentation"
      // onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      <List>
        <ListItem  disablePadding>
          <ListItemButton href="/hugo">
            <ListItemText primary="Hugo"/>
          </ListItemButton>
        </ListItem>
      </List>
      <Divider />
      <List>
        <ListItem button disablePadding sx={{ml:2, width: 220}} onClick={handleClick}>
          <ListItemText primary="회사소개" />
          {openSubmenu ? <ExpandLess sx={{ ml: 1, mt:2 }} /> : <ExpandMore sx={{ ml: 1 }} />}
        </ListItem>
        <Collapse in={openSubmenu} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            {['소개', '인사말', '오시는길'].map((text, index) => (
              <ListItem key={text} disablePadding>
                <ListItemButton >
                  <ListItemText primary={<Typography sx={{ fontSize: '14px' }}>{text}</Typography>}/>
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        </Collapse>
      </List>
      <Divider />
      <List>
        <ListItem  disablePadding >
          <ListItemButton href="/pr">
            <ListItemText primary="홍보자료"/>
          </ListItemButton>
        </ListItem>
      </List>
    </Box>
  );

  return (
    <nav className={styles.appbar_container}>
      <AppBar position="fixed" sx={{backgroundColor: '#2D5DA6'}}>
        <Toolbar         
          sx={{ 
            color: 'white',
          }}
        >
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            //닫히려면 사이드바 안의 메뉴를 선택하거나 사이드바 밖 터치 
            // onClick={toggleDrawer(true)}
            //닫을때 어디를 눌러도 닫히는 코드
            onClick={() => setState({ ...state, left: !state.left })}
          >
            <MenuIcon />
          </IconButton>
          <Drawer
            anchor="left"
            open={state.left}
            onClose={toggleDrawer(false)}
          >
            {list()}
          </Drawer>

          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }} className={styles.logoDiv}>
            <p className={styles.logeText}>HUMANDEEP</p>
          </Typography>
        </Toolbar>
      </AppBar>
    </nav>
  );
}

export default Appbar;
