import styles from "./PR.module.css";

function PR () {
  return (
    <div>
      {/* 상단 네브바와 겹칠 이미지 */}
      <div className={styles.bigImg} >
        <img 
          src={require('assets/Images/Humandeep/subPages/subMain.png')} 
          alt="" 
          className={styles.imgSizs} 
        />
        <div className={styles.titleTextCard}>
          <p className={styles.smallText}>홍보자료</p>
          <p className={styles.bigText}>PR</p>
        </div>
      </div>
    </div>
  )
}

export default PR;